/* Google Fonts */
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,600;0,700;1,600&display=swap");

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  scroll-padding-top: 1rem;
  scroll-behavior: smooth;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

/*Variables*/
:root {
  --main-color: #d90429;
  --text-color: #020102;
  --bg-color: #fff;
}

home {
  padding: 4rem 0 2rem;
}

img {
  width: 100%;
}

body {
  color: var(--text-color);
}
.container {
  max-width: 1068px;
  margin-left: auto;
  margin-right: auto;
}
.header {
  display: block;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
.Nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 35;
}
#menu-icon {
  font-size: 24px;
  cursor: pointer;
  color: var(--text-color);
  display: none;
}
.logo {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-color);
}
.logo span {
  color: var(--main-color);
}
.navbar {
  display: flex;
  column-gap: 2rem;
}
.navbar a {
  color: var(--text-color);
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
}
.navbar a:hover,
.navbar .active {
  color: var(--main-color);
  border-bottom: 3px solid;
}
#search-icon {
  font-size: 24px;
  cursor: pointer;
  color: var(--main-color);
}

.search-box {
  position: absolute;
  top: 110%;
  right: 0;
  left: 0;
  background: var(--bg-color);
  box-shadow: 4px 4px 20px rgb(15 54 55/10%);
  border: 1px solid var(--main-color);
  border-radius: 0.5rem;
  clip-path: circle(0% at 100% 0%);
}
.search-box.active {
  clip-path: circle(144% at 100% 0%);
  /*transition: 0.4s;*/
}

.search-box input {
  width: 100%;
  padding: 20px;
  border: none;
  outline: none;
  background: transparent;
  font-size: 1rem;
}
.home {
  max-width: 1300px;
  margin: auto;
  width: 100%;
  min-height: 640px;
  display: flex;
  align-items: center;
  background: url(./../img/TeslaHome.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center left;
}
.home-text {
  padding-left: 130px;
}
.home-text h1 {
  font-size: 2.4rem;
}
.home-text span {
  color: var(--main-color);
}
.home-textp {
  font-size: 0.938rem;
  font-weight: 300;
  margin: 0.5rem 0 1.2rem;
}
.btn {
  padding: 10px 22px;
  background: var(--main-color);
  color: var(--bg-color);
  font-weight: 400;
}
.btn:hover {
  background: #fd052f;
}
.heading {
  text-align: center;
}
.heading span {
  font-weight: 500;
  color: var(--main-color);
}
.heading p {
  font-size: 0.938rem;
  font-weight: 300;
}
.cars-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  margin-top: 2rem;
}
.cars-container .box {
  flex: 1 1 17rem;
  position: relative;
  height: 200px;
  border-radius: 0.5rem;
  overflow: hidden;
}
.cars-container .box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
}
.cars-container .box img:hover {
  transform: scale(1.1);
  transition: 0.5s;
}
.cars-container .box h2 {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  font-weight: 400;
  font-size: 1rem;
  background: var(--bg-color);
  padding: 8px;
  border-radius: 0.5rem;
}
.cars-container .box:hover h2 {
  background: var(--main-color);
  color: var(--bg-color);
}
.about {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 1.5rem;
}
.about-img {
  flex: 1 1 21rem;
}
.about-text {
  flex: 1 1 21rem;
}
.about-text span {
  font-weight: 500;
  color: var(--main-color);
}
.about-text h2 {
  font-size: 1.7s;
}
.about-text p {
  font-size: 0.938rem;
  margin: 0.5rem 0 1.4rem;
}



/*----------------------------------------------------*/

input {
  display: none;
  position: absolute;
}

.head {
  position: relative;
  width: 80%;
  margin: auto;
  height: 100px;
  margin-top: 60px;
  animation: trans 0.7 linear forwards;
}

ul.nav {
  padding-top: 15px;
  z-index: 100;
  position: absolute;
  width: 100%;
  margin-top: -40px;
}

ul.nav li {
  list-style: none;
  float: left;
  width: 25%;
  text-align: center;
  color: #aaa;
  text-transform: uppercase;
  font-size: 11px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s ease;
  vertical-align: middle;
  height: 60px;
  position: relative;
}

ul.nav li::before {
  content: "";
  width: 25px;
  height: 25px;
  border: 2px solid #aaa;
  border-radius: 50%;
  margin: auto;
  display: block;
  transition: all 0.3s ease;
}

ul.nav li:hover::before {
  border: 2px solid #fff;
  background: #777;
}

ul.nav li .inner {
  position: absolute;
  width: 100%;
  bottom: 0;
  font-size: 14px;
  font-weight: bolder;
}

ul.nav li.active,
ul.nav li:hover {
  color: #444;
}

ul.nav li::after {
  content: "";
  display: block;
  width: 7px;
  height: 7px;
  background: #777;
  border-radius: 50%;
  margin: auto;
  border: 7px solid #fff;
  margin-top: 40px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
}

ul.nav li.hover::after,
ul.nav li.active::after {
  background: blue;
}

.line {
  width: 76%;
  margin: auto;
  background: #eee;
  height: 7px;
  position: absolute;
  left: 115;
  top: 52px;
  z-index: 1;
  border-radius: 50px;
}

.line span {
  content: "";
  width: 2%;
  height: 100%;
  background-image: linear-gradient(to right, #a29bfe 0%, #ff7675 100%);
  position: absolute;
  z-index: 2;
  border-radius: 50px;
  transition: all 0.8s ease;
}

.content {
  position: relative;
  display: flex;
  justify-content: center;
  animation: anim 0.7s linear forwards;
}

section {
  width: 70%;
  margin: auto;
  background-image: linear-gradient(to right, #a29bfe 0%, #ff7675 100%);
  border-radius: 4px;
  padding: 30px;
  color: #fff;
}

section span {
  font-size: 3rem;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

h1 {
  font-weight: 900;
  margin-top: 10px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 4px;
}

section p {
  padding: 30px 48px;
  text-align: center;
  font-weight: bolder;
  font-size: 13px;
}

.profile:checked ~ .head .nav li.stl::before,
.settings:checked ~ .head .nav li.stl::before,
.settings:checked ~ .head .nav li.st2::before,
.posts:checked ~ .head .nav li.stl::before,
.posts:checked ~ .head .nav li.st2::before,
.posts:checked ~ .head .nav li.st3::before,
.books:checked ~ .head .nav li.stl::before,
.books:checked ~ .head .nav li.st2::before,
.books:checked ~ .head .nav li.st3::before,
.books:checked ~ .head .nav li.st4::before {
  border: 2px solid #fff;
  background: #777;
  transition: all 0.3s ease;
}
.settings:checked ~ .head .line span {
  width: 35%;
}
.posts:checked ~ .head .line span {
  width: 68%;
}
.books:checked ~ .head .line span {
  width: 100%;
}
.profile-section,
.account-section,
.post-section,
.advanced-section {
  transform: scale(0);
  transform-origin: center-bottom;
  transition: all 0.7s ease;
  position: absolute;
  top: 0;
}
.profile:checked ~ .content .profile-section,
.settings:checked ~ .content .account-section,
.posts:checked ~ .content .post-section,
.books:checked ~ .content .advanced-section {
  display: block;
  transform: scale(1);
  transform-origin: bottom;
}
@keyframes anim {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  50% {
    opacity: 0.5;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes trans {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  50% {
    opacity: 0.5;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
